import Vue from 'vue'
import Router from 'vue-router'
import About from '@/components/About'
import Contact from '@/components/Contact'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/about',
    }, {
      path: '/contact',
      name: 'Contact',
      component: Contact
    }, {
      path: '/about',
      name: 'About',
      component: About
    }
  ]
})



