<template>
  <div class="main__container_about">
    <div id="d_m_p" class="container__animated presentation">
      <div class="name">Esteban Córdoba</div>
      <div class="charge">Data Engineer at Latin Checkout</div>
      <div class="sub_charge">Web software developer</div>
    </div>
    <div id="d_m_i" class="info">
      <div class="d_e" id="d_e">
        <a href="mailto:estebancores@gmail.com">
          <span class="s_f">estebancores@gmail.com</span>
          <span class="s_l">E - mail</span>
        </a>
      </div>
      <div class="d_f" id="d_f">
        <a href="https://www.facebook.com/esteban.cordobarestrepo" target="_blank">
          <span class="s_f">@esteban.cordobarestrepo</span>
          <span class="s_l">Facebook</span>
        </a>
      </div>
      <div class="d_ink" id="d_ink">
        <a href="https://www.linkedin.com/in/sdestebancores/" target="_blank">
          <span class="s_f">@estebancores</span>
          <span class="s_l">Linked In</span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'About',
    mounted() {
      let p_div = document.getElementById('d_m_p');
      let de_div = document.getElementById('d_e');
      let df_div = document.getElementById('d_f');
      let dink_div = document.getElementById('d_ink');
      setTimeout(() => {
        p_div.classList.add('showing');
      }, 300);

      setTimeout(() => {
        de_div.classList.add('showing');
      }, 600);
      setTimeout(() => {
        df_div.classList.add('showing');
      }, 800);
      setTimeout(() => {
        dink_div.classList.add('showing');
      }, 1000);

    },
    data() {
      return {}
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main__container_about {
    height: 93vh;
    box-sizing: border-box;
    position: relative;
  }

  .container__animated {
    position: absolute;
    opacity: 0;
    transform: translateY(-30px);
    -webkit-transition: all 1.5s;
    -moz-transition: all 1.5s;
    -ms-transition: all 1.5s;
    -o-transition: all 1.5s;
    transition: all 1.5s;
  }

  .presentation {
    height: 95px;
    top: 50%;
    left: -5%;
  }

  .info {
    position: absolute;
    height: 60px;
    top: 80%;
    left: -18%;
    z-index: 1000;
    transform: translateY(-30px);
    -webkit-transition: all .1s;
    -moz-transition: all .1s;
    -ms-transition: all .1s;
    -o-transition: all .1s;
    transition: all .1s;
  }

  .info .d_e a, .info .d_f a, .info .d_ink a {
    text-decoration: none;
  }

  .d_e, .d_f, .d_ink {
    opacity: 0;
    transform: translateY(-60px);
    -webkit-transition: all 1.5s;
    -moz-transition: all 1.5s;
    -ms-transition: all 1.5s;
    -o-transition: all 1.5s;
    transition: all 1.5s;
  }

  .info div {
    display: inline-block;
    margin-right: 200px;
  }

  .info div:last-child {
    margin-right: 0;
  }

  .s_f, .s_l {
    display: block;
    color: #000;
  }

  .s_f {
    font-weight: bold;
    font-size: 20px;
  }

  .s_l {
    font-size: 15px;
  }

  .showing {
    opacity: 1;
    transform: translateY(0);
  }

  .name {
    color: #000;
    font-size: 44px;
    font-weight: bold;
  }

  .charge {
    color: #000;
    font-size: 35px;
  }

  .sub_charge {
    color: #000;
    font-size: 28px;
  }

  .images__container {
    position: absolute;
    right: 5%;
    top: 0;
    width: 600px;
    height: 93vh;
  }

  .photos__container {
    width: 100%;
    height: 100%;
    position: relative;
  }

  .photos__container div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transition: all .1s;
    -moz-transition: all .1s;
    -ms-transition: all .1s;
    -o-transition: all .1s;
    transition: all .1s;

  }

</style>



