<template>
  <div id="app">
    <div id="page__navigator">
      <ul class="page__navigator__menu">
        <div style="width: auto;float: right;margin: 5px 100px 5px 0;">
          <li class="menu__option">
            <div class="link__container">
              <router-link tag="a" to="/about">About</router-link>
            </div>
            <div class="bottom__border"></div>
          </li>
          <li class="menu__option">
            <div class="link__container">
              <router-link tag="a" to="/contact">Contact</router-link>
            </div>
            <div class="bottom__border"></div>
          </li>
        </div>
      </ul>
    </div>
    <div class="router__container">
      <transition name="fade" mode="out-in">
        <router-view/>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'App',
  }
</script>

<style>
  #app {
    background: #fff;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
  }
  .router__container {
    width: 80%;
    height: 93vh;
    margin: 0 0 0 272px;
    box-sizing: border-box;
    background: rgba(0, 0, 0, .05);
  }
  .page__navigator__menu {
    height: 40px;
  }

  .menu__option {
    display: inline-block;
    margin: 0 10px;
  }

  .link__container a {
    text-decoration: none;
    color: #000;
  }

  .bottom__border {
    background: #000;
    width: 0;
    height: 2px;
    margin: 0 auto;
    border-radius: 5px;
    -webkit-transition: all .1s ease;
    -moz-transition: all .1s ease;
    -ms-transition: all .1s ease;
    -o-transition: all .1s ease;
    transition: all .1s ease;
  }

  .menu__option:hover .bottom__border {
    width: 100%;
    margin: 0 auto;

  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .2s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

</style>



